import { createElement } from '../render.js';
import { getCorrectDateFromat } from '../utils.js';
import dayjs from 'dayjs';

function createOfferTemplate({title, price}) {
  return (`
  <li class="event__offer">
        <span class="event__offer-title">${title}</span>
          &plus;&euro;&nbsp;
        <span class="event__offer-price">${price}</span>
  </li>`);
}

function createDestinationPointTemplate(point, offers, destination) {
  const {type, dateFrom, dateTo, isFavorite, price} = point;
  const {name} = destination;

  const favoriteClassName = isFavorite ? 'event__favorite-btn--disabled' : 'event__favorite-btn--active';


  return (
    `<li class="trip-events__item">
    <div class="event">
      <time class="event__date" datetime=${dayjs(dateFrom).format('YYYY-MM-D')}>${getCorrectDateFromat(dateFrom)}</time>
      <div class="event__type">
        <img class="event__type-icon" width="42" height="42" src="img/icons/${type}.png" alt="Event type icon">
      </div>
      <h3 class="event__title">${type} ${name}</h3>
      <div class="event__schedule">
        <p class="event__time">
          <time class="event__start-time" datetime=${dateFrom}>10:30</time>
          &mdash;
          <time class="event__end-time" datetime=${dateTo}>11:00</time>
        </p>
        <p class="event__duration">30M</p>
      </div>
      <p class="event__price">
        &euro;&nbsp;<span class="event__price-value">${price}</span>
      </p>
      <h4 class="visually-hidden">Offers:</h4>
      <ul class="event__selected-offers">
        ${offers.map((offer) => createOfferTemplate(offer)).join('')}
      </ul>
      <button class="event__favorite-btn ${favoriteClassName}" type="button">
        <span class="visually-hidden">Add to favorite</span>
        <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
          <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
        </svg>
      </button>
      <button class="event__rollup-btn" type="button">
        <span class="visually-hidden">Open event</span>
      </button>
    </div>
  </li>`
  );
}

export default class DestinationPointView {
  constructor({point, offers, destination}) {
    this.point = point;
    this.offers = offers;
    this.destination = destination;

  }

  getTemplate(){
    return createDestinationPointTemplate(this.point, this.offers, this.destination);
  }

  getElement() {
    if (!this.element) {
      this.element = createElement(this.getTemplate());
    }
    return this.element;
  }

  removeElement() {
    this.element = null;
  }
}
